// @ts-nocheck
import React, { useEffect, useState, useContext } from 'react';
import { Card, Button } from "@material-ui/core";

//var index = 0;

export const Tv = ({ address, objkts }) => {

    const [play, setPlay] = useState(true);
    const [current, setCurrent] = useState();
    const [tokens, setTokens] = useState([]);
    const [loaded, setLoaded] = useState([]);
    const [counter, setCounter] = useState(0);


    function handleImageLoaded(element) {
        //console.log("loaded")
        //console.log(element.target)
        setLoaded([...loaded, { image: element.target.currentSrc, id: element.target.title }])
    }

    useEffect(() => {

        function zap() {

            console.log("zap " + counter)
            if (loaded) {

                if (counter < loaded.length)
                    setCounter(prevCount => prevCount + 1);
                else
                    setCounter(0);

                setCurrent(loaded[counter])
            }

        }

        if (play) {
            const interval = setInterval(zap, 5000);
            return () => {
                clearInterval(interval)
            };
        }
    }, [play, loaded, counter]);

    return (
        <div onClick={() => { setPlay(!play) }} style={{ width: "100%", height: "100%" }}>

            {
                objkts && objkts.map((token, index) => (
                    <div key={index}>
                        {token && token.display_uri &&
                            <div style={{ width: "100%", height: "100%", marginLeft: "auto", marginRight: "auto", display: current && current.image === token.display_uri.replace('ipfs://', 'https://ipfs.io/ipfs/') && current.id === token.token_id ? "block" : "none" }}>

                                <Button style={{ marginTop: 15, width: "100%" }}
                                    onClick={() => {
                                        window.open(`https://objkt.com/asset/${token.fa2_address}/${token.token_id}`, "_blank")
                                    }}
                                >

                                    <Card variant="elevation" elevation={24}>
                                        <div style={{ textAlign: "center", fontSize: "1.1em" }}> <b>{token.name}</b></div>
                                        <img onLoad={e => handleImageLoaded(e)} src={token.display_uri.replace('ipfs://', 'https://ipfs.io/ipfs/')} title={token.token_id} style={{ width: "100%" }} />

                                        <div style={{ textAlign: "center" }}> <b>{token.supply} X </b></div>
                                        <div style={{ display: "flex" }}>
                                            <div>
                                                <b>
                                                    price: {token.lowest_ask ? ((token.lowest_ask / 1000000) + "") : "-"}
                                                </b>
                                            </div>
                                            <div style={{ marginLeft: "auto" }}>
                                                <b>
                                                    offer: {token.highest_offer ? ((token.highest_offer / 1000000) + "") : "-"}
                                                </b>
                                            </div>
                                        </div>
                                    </Card>
                                </Button>
                            </div>
                        }
                    </div>

                ))
            }

            {
                false && tokens && tokens.map(({ token }) => (
                    <>
                        {token && token.display_uri &&
                            <div key={token.id} style={{ width: "100%", height: "100%", marginLeft: "auto", marginRight: "auto", display: current === token.display_uri.replace('ipfs://', 'https://ipfs.io/ipfs/') ? "block" : "none" }}>


                                <Card variant="elevation" elevation={24}>
                                    <div style={{ textAlign: "center", fontSize: "1.1em" }}> <b>{token.name}</b></div>
                                    <img onLoad={e => handleImageLoaded(e)} src={token.display_uri.replace('ipfs://', 'https://ipfs.io/ipfs/')} title={"thumbnail"} style={{ width: "100%" }} />

                                    <div style={{ textAlign: "center" }}> <b>{token.supply} X </b></div>
                                    <div style={{ display: "flex" }}>
                                        <div>
                                            <b>
                                                price: {token.lowest_ask ? ((token.lowest_ask / 1000000) + "") : "-"}
                                            </b>
                                        </div>
                                        <div style={{ marginLeft: "auto" }}>
                                            <b>
                                                offer: {token.highest_offer ? ((token.highest_offer / 1000000) + "") : "-"}
                                            </b>
                                        </div>
                                    </div>
                                </Card>
                            </div>
                        }
                    </>

                ))
            }


        </div >
    );
}

const query_collection = `
query collectorGallery($address: String!) {
  hic_et_nunc_token_holder(where: {holder_id: {_eq: $address}, token: {creator: {address: {_neq: $address}}}, quantity: {_gt: "0"}}, order_by: {token_id: desc}) {
	token {
	  id
	  artifact_uri
	  display_uri
	  thumbnail_uri
	  timestamp
	  mime
	  title
	  description
	  supply
	  royalties
	  creator {
		address
	  }
	}
  }
}
`