// @ts-nocheck
import "./App.css";
import React, { useEffect, useState } from "react";

//ui
import { Button, TextField, Card, IconButton } from "@material-ui/core";

//logic

//import _ from "underscore";
//import { FirebaseContext } from "./firebaseContext";
import { Profile } from './components/profile';
import { useNavigate } from "react-router-dom";
import { ProfileList } from "./components/ProfileList";

import discord from "./assets/discord-192.png";
import fa2MultiAsset from "./components/assets/MultiAsset.json";
import useBeacon from "./components/useBeacon";



function App() {
  const navigate = useNavigate();

  const [address, setAddress] = useState();
  const [showList, setShowList] = useState(false);

  const { pkh, Tezos, sync, unsync, isConnected } = useBeacon();

  const deployContract = async (input) => {
    //console.log("deploy contract")
    console.log(input)

    await Tezos.wallet
      .originate({
        code: fa2MultiAsset,
        storage: input,
      })
      .send();

  }

  useEffect(() => {
    async function getAddress() {
      ////console.log(new URL(window.location.href).searchParams.get('profile'))
      let profileAddress = new URL(window.location.href).searchParams.get('profile');
      let list = new URL(window.location.href).pathname;
      ////console.log(list)
      //setAddress(profileAddress);
      if (list === "/directory") {
        setShowList(true);
      }
      else {
        if (!(profileAddress && profileAddress.includes(".tez"))) {
          setAddress(profileAddress);
        }
        else if (profileAddress) {

          await fetch('https://api.tezos.domains/graphql', {
            method: 'POST',
            headers: {
              'Content-Type': 'application/json',
            },
            body: JSON.stringify({
              query: `
                {
                  domain(name: "` + profileAddress + `") {
                    name
                    address
                  }
                }
                `,
              variables: {
              },
            }
            ),
          })
            .then((res) => res.json())
            .then((result) => {
              ////console.log(result);
              if (result.data.domain) {
                setAddress(result.data.domain.address);
              }
            });
        }
      }
    }
    getAddress();
  }, [setAddress]);

  useEffect(() => {
    if (pkh && !address) {
      navigate("/?profile=" + pkh);
    }
  }, [pkh, address]);


  return (
    <div>
      <div
        className="top-left"
        style={{
          fontSize: "1.5em",
          display: "flex",
          alignItems: "center",
          margin: 6,
          fontFamily: "Neue-bold",
          marginRight: 22,
        }}
      >
        <b>Profile Assemblr </b>
      </div>



      <div style={{ position: "absolute", top: 4, left: "auto", right: "auto", fontFamily: "Neue-regular", width: "100%" }}>

        <div style={{ textAlign: "center", fontSize: "1.1em", fontFamily: "Neue-regular" }}>
          {address ?
            <div>
              Auto Generated Profile for Tezos Wallet Address {address} Sync to Claim
            </div> :
            <div>
              Auto Generated Profile for Tezos
            </div>
          }
        </div>

      </div>

      {
        (address || pkh) && !showList &&
        <Profile
          key={pkh + address}
          address={address ? address : pkh}
          account={pkh}
          setAddress={setAddress}
          navigate={navigate}
          deployContract={deployContract}
        />
      }
      {showList && <ProfileList />}
      <div
        className="top-right"
        style={{ position: "absolute", display: "flex", alignItems: "center" }}
      >
        {isConnected && (
          <Button
            size={"small"}
            title={"unsync"}
            onClick={() => {
              unsync();
            }}
          >
            <u>unsync</u>{" "}
          </Button>
        )}

        {isConnected && <div> | </div>}
        <Button
          title={"sync"}
          size={"small"}
          onClick={async () => {
            //console.log(1)
            sync().catch(console.log);
            //console.log(-1)

          }}
        >
          <u>{isConnected ? pkh : "sync"}</u>{" "}
        </Button>
      </div>

      <div
        className="bottom"
        style={{ position: "sticky" }}
      >
        <div
          style={{ position: "sticky", display: "flex", alignItems: "center", justifyContent: "space-between", backgroundColor: "white", width: "100%", overflow: "scroll" }}
        >
          <Button
            style={{ marginInline: 2, minWidth: 150 }}
            size={"small"}
            onClick={async () => {
              window.open("https://adventurenetworks.net/#/", "_blank");
            }}

          >
            <u>Adventure Network</u>{" "}
          </Button>
          <IconButton
            onClick={() => { window.open("https://discord.gg/eHemqMfFAV") }}
          >
            <img src={discord} alt="discord" width="20" height="20"></img>
          </IconButton>

          <Button
            style={{ marginInline: 2, minWidth: 100 }}
            size={"small"}
            onClick={async () => {
              //window.open("https://assemblr.xyz/directory", "_blank");
              navigate("/discovery");
            }}

          >
            <u>DISCOVERY</u>{" "}
          </Button>

          <Button
            style={{ marginInline: 2, minWidth: 100 }}
            size={"small"}
            onClick={async () => {
              //window.open("https://assemblr.xyz/directory", "_blank");
              navigate("/directory");
            }}

          >
            <u>Directory</u>{" "}
          </Button>

          <Button
            style={{ marginInline: 2, minWidth: 150 }}
            size={"small"}
            onClick={async () => {
              window.open("https://trydrum.net/", "_blank");
            }}
          >
            <u> TryDrum</u>{" "}
          </Button>

          <Button
            style={{ marginInline: 2, minWidth: 150 }}
            size={"small"}
            onClick={async () => {
              window.open("https://peopleparty.xyz/", "_blank");
            }}
          >
            <u> People Party </u>{" "}
          </Button>

          <Button
            style={{ marginInline: 2, minWidth: 150 }}
            size={"small"}
            onClick={async () => {
              window.open("https://elementals.run/", "_blank");
            }}
          >
            <u> Elementals </u>{" "}
          </Button>

          <Button
            style={{ marginInline: 2, minWidth: 150 }}
            size={"small"}
            onClick={async () => {
              window.open("https://network1.cc/", "_blank");
            }}
          >
            <u> Network 1 </u>{" "}
          </Button>

          <Button
            style={{ marginInline: 2, minWidth: 150 }}
            size={"small"}
            onClick={async () => {
              window.open("https://adventurepizza.github.io/Network2/", "_blank");
            }}
          >
            <u> Network 2 </u>{" "}
          </Button>

          <Button
            style={{ marginInline: 2, minWidth: 150 }}
            size={"small"}
            onClick={async () => {
              window.open("https://adventurepizza.github.io/Network3/", "_blank");
            }}
          >
            <u> Network 3 </u>{" "}
          </Button>

          <Button
            style={{ marginInline: 2, minWidth: 150 }}
            size={"small"}
            onClick={async () => {
              window.open("https://adventurepizza.github.io/Network4/", "_blank");
            }}
          >
            <u> Network 4 </u>{" "}
          </Button>

          <Button
            style={{ marginInline: 2, minWidth: 150 }}
            size={"small"}
            onClick={async () => {
              window.open("https://asknetwork.xyz/", "_blank");
            }}
          >
            <u> Network 5 </u>{" "}
          </Button>

          <Button
            style={{ marginInline: 2, minWidth: 150 }}
            size={"small"}
            onClick={async () => {
              window.open("https://soundsofpi.com/", "_blank");
            }}
          >
            <u> Sounds of Pi </u>{" "}
          </Button>

          <Button
            style={{ marginInline: 2, minWidth: 150 }}
            size={"small"}
            onClick={async () => {
              window.open("https://www.supmail.net/", "_blank");
            }}
          >
            <u> Sup Mail </u>{" "}
          </Button>

          <Button
            style={{ marginInline: 2, minWidth: 150 }}
            size={"small"}
            onClick={async () => {
              window.open("https://metaverses.fyi/#/", "_blank");
            }}
          >
            <u> Metaverses.fyi </u>{" "}
          </Button>
        </div>
      </div>
    </div >
  );
}

export default App;
